<template>
  <div class="content">
    <div class="category">
      <div class="category__item" v-for="(item, index) in category" :key="index"
           :class="{'category__item--active': index === 0}">
        {{ item }}
      </div>
    </div>
    <div class="product">
      <div class="product__item" v-for="(item, index) in 10" :key="index">
        <img class="product__item__img" src="http://www.dell-lee.com/imgs/vue3/near.png" alt="">
        <div class="product__item__detail">
          <div class="product__item__title">番茄250g/份</div>
          <div class="product__item__sales">月售10件</div>
          <div class="product__item__price">
            <span class="product__item__yen">&yen;</span>
            <span class="product__item__now">33.6</span>
            <span class="product__item__origin">&yen;66.6</span>
          </div>
        </div>
        <div class="product__number">
          <span class="iconfont product__number__minus">&#xe8b1;</span>
          <span class="product__number__count">0</span>
          <span class="iconfont product__number__plus">&#xe8c8;</span>
        </div>
      </div>
    </div>
  </div>
  <Toast v-if="toastInfo.show" :message="toastInfo.message"/>
</template>

<script>
import { get } from '@/utils/request'
import Toast, { useToastEffect } from '@/components/Toast.vue'

export default {
  name: 'ContentView',
  components: { Toast },
  setup () {
    const category = ['全部商品', '秒杀', '新鲜水果', '休闲食品', '时令蔬菜', '肉蛋家禽']
    const {
      toastInfo,
      showToast
    } = useToastEffect()
    const getContentData = async () => {
      try {
        const result = await get('/api/shop/1/products', {
          tab: 'all'
        })
        if (result.errno !== 0) return showToast(result.desc)
        console.log(result)
      } catch (e) {
        showToast(e.message)
      }
    }
    getContentData()
    return {
      category,
      toastInfo
    }
  }
}
</script>

<style lang="scss" scoped>
@import "../../style/variables";
@import "../../style/mixins";

.content {
  display: flex;
  position: absolute;
  top: 1.54rem;
  bottom: .5rem;
  left: 0;
  right: 0;
}

.category {
  width: .76rem;
  background: $search-bgColor;
  overflow: auto;

  &__item {
    line-height: .4rem;
    text-align: center;
    font-size: .14rem;
    color: $content-fontcolor;

    &--active {
      background: $bgColor;
    }
  }
}

.product {
  flex: 1;
  overflow: auto;

  &__item {
    display: flex;
    padding: .12rem 0;
    margin: 0 .18rem 0 .16rem;
    border-bottom: .01rem solid $content-bgColor;
    position: relative;

    &__img {
      width: .68rem;
      height: .68rem;
      margin-right: .16rem;
    }

    &__detail {
      flex: 1;
      overflow: hidden;
    }

    &__title {
      line-height: .2rem;
      font-size: .14rem;
      font-weight: bold;
      color: $content-fontcolor;
      @include ellipsis;
    }

    &__sales {
      margin: .06rem 0;
      line-height: .16rem;
      font-size: .12rem;
      color: $content-fontcolor;
    }

    &__price {
      line-height: .2rem;
      font-size: .14rem;
      color: $highlight-fontColor;
    }

    &__yen {
      font-size: .1rem;
    }

    &__now {
      font-weight: bold;
    }

    &__origin {
      font-size: .1rem;
      color: $light-fontColor;
      text-decoration: line-through;
      margin-left: .06rem;
    }
  }

  &__number {
    position: absolute;
    right: 0;
    bottom: .12rem;
    height: .2rem;
    line-height: .2rem;

    .iconfont {
      font-size: .25rem;
    }

    &__minus {
      color: $medium-fontColor;
    }

    &__plus {
      color: $btn-bgColor;
    }

    &__count {
      font-size: .14rem;
      color: $content-fontcolor;
      margin: 0 .1rem;
      vertical-align: top;
    }
  }
}
</style>
