<template>
  <div class="wrapper">
    <div class="top">
      <div class="top__back">
        <span class="iconfont" @click="handleBackClick">&#xe600;</span>
      </div>
      <div class="search">
        <span class="iconfont">&#xe8ba;</span>
        <input type="text" class="search__input" placeholder="请输入商品名称搜索">
      </div>
    </div>
    <ShopInfo :item="data.item" :hide-border="true" v-show="data.item.imgUrl"/>
    <Content/>
  </div>
  <Toast v-if="toastInfo.show" :message="toastInfo.message"/>
</template>

<script>
import { reactive } from 'vue'
import { useRouter, useRoute } from 'vue-router'
import { get } from '@/utils/request'
import ShopInfo from '@/components/ShopInfo.vue'
import Content from '@/views/shop/Content.vue'
import Toast, { useToastEffect } from '@/components/Toast.vue'

export default {
  name: 'ShopView',
  components: {
    ShopInfo,
    Content,
    Toast
  },
  setup () {
    const router = useRouter()
    const route = useRoute()
    const data = reactive({ item: {} })
    const {
      toastInfo,
      showToast
    } = useToastEffect()
    const getItemData = async () => {
      try {
        const result = await get(`/api/shop/${route.params.id}`)
        if (result?.errno !== 0) return showToast(result.desc)
        data.item = result.data
      } catch (e) {
        showToast(e.message)
      }
    }
    getItemData()
    const handleBackClick = () => {
      router.back()
    }
    return {
      data,
      toastInfo,
      handleBackClick
    }
  }
}
</script>

<style lang="scss" scoped>
@import "../../style/variables";

.wrapper {
  padding: 0 .18rem;
}

.top {
  display: flex;
  height: .32rem;
  line-height: .32rem;
  padding: .16rem 0 .04rem;

  &__back {
    width: .28rem;

    .iconfont {
      font-size: 0.25rem;
      color: #B6B6B6;
    }
  }
}

.search {
  display: flex;
  flex: 1;
  background: $search-bgColor;
  border-radius: .16rem;

  .iconfont {
    color: $search-fontColor;
    font-size: 0.18rem;
    padding: 0 .12rem 0 .16rem;
  }

  &__input {
    flex: 1;
    border: none;
    outline: none;
    background: none;
    padding: 0 .16rem 0 0;
    font-size: .14rem;

    &::placeholder {
      color: $search-fontColor;
    }
  }
}
</style>
